/* Sizes */
@ring-small-1: 230px;
@ring-small-2: 190px;

@ring-medium-1: 450px;
@ring-medium-2: 362px;
@ring-medium-3: 348px;
@ring-medium-4: 336px;
@ring-medium-5: 316px;
@ring-medium-6: 290px;
@ring-medium-7: 256px;

@ring-large-1: 560px;
@ring-large-2: 450px;
@ring-large-3: 432px;
@ring-large-4: 416px;
@ring-large-5: 392px;
@ring-large-6: 360px;
@ring-large-7: 318px;


.rings-small {
    position: relative;
    height: 230px;
    width: 230px;
    margin: 0 auto;

    .ring {
        position: absolute;
        display: block;
        background-repeat: no-repeat;
        background-position: center center;
    }

    .one {
        width: @ring-small-1;
        height: @ring-small-1;
        background-size: @ring-small-1 @ring-small-1;
        background-image: url('../img/rings/ring_01.png');
        top: 0; right: 0;
    }

    .two {
        width: @ring-small-2;
        height: @ring-small-2;
        background-size: @ring-small-2 @ring-small-2;
        background-image: url('../img/rings/ring_05.png');
        top: 20px; right: 20px;
    }


}


.rings-medium {
    position: relative;
    height: 450px;
    margin: 0 auto;

    .debug {background-color: rgba(255,255,255,0.1);}

    .ring {
        position: absolute;
        left: 0;
        display: block;
        background-repeat: no-repeat;
        background-position: center center;
        transition: all 0.5s ease;
        -webkit-transition: all 0.5s ease;
    }

    .one {
        width: @ring-medium-1;
        height: @ring-medium-1;
        background-size: @ring-medium-1 @ring-medium-1;
        background-image: url('../img/rings/ring_01.png');
        top: 0; left: 0;
    }

    .two {
        width: @ring-medium-2;
        height: @ring-medium-2;
        background-size: @ring-medium-2 @ring-medium-2;
        background-image: url('../img/rings/ring_02.png');
        top: ((@ring-medium-1 - @ring-medium-2) / 2); left: ((@ring-medium-1 - @ring-medium-2) / 2);
    }

    .three {
        width: @ring-medium-3;
        height: @ring-medium-3;
        background-size: @ring-medium-3 @ring-medium-3;
        background-image: url('../img/rings/ring_03.png');
        top: ((@ring-medium-1 - @ring-medium-3) / 2); left: ((@ring-medium-1 - @ring-medium-3) / 2);
    }

    .four {
        width: @ring-medium-4;
        height: @ring-medium-4;
        background-size: @ring-medium-4 @ring-medium-4;
        background-image: url('../img/rings/ring_04.png');
        top: ((@ring-medium-1 - @ring-medium-4) / 2); left: ((@ring-medium-1 - @ring-medium-4) / 2);
    }

    .five {
        width: @ring-medium-5;
        height: @ring-medium-5;
        background-size: @ring-medium-5 @ring-medium-5;
        background-image: url('../img/rings/dash_01.png');
        top: ((@ring-medium-1 - @ring-medium-5) / 2); left: ((@ring-medium-1 - @ring-medium-5) / 2);
    }

    .six {
        width: @ring-medium-6;
        height: @ring-medium-6;
        background-size: @ring-medium-6 @ring-medium-6;
        background-image: url('../img/rings/dash_02.png');
        top: ((@ring-medium-1 - @ring-medium-6) / 2); left: ((@ring-medium-1 - @ring-medium-6) / 2);
    }

    .seven {
        width: @ring-medium-7;
        height: @ring-medium-7;
        background-size: @ring-medium-7 @ring-medium-7;
        background-image: url('../img/rings/ring_05.png');
        top: ((@ring-medium-1 - @ring-medium-7) / 2); left: ((@ring-medium-1 - @ring-medium-7) / 2);
    }

}


